import React, { useState } from 'react'
import Color from "color"

export default class TypedText extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            speed: props.speed || 1,
            textStyle: props.textStyle || {color:"white"},

            phrases: props.text.split('\n'),

            charIndex:0,
            phraseIndex:0,

            text: props.text,
            renderText: "",
            renderItems : [],
            index: -1,
            char: "",
        }
    }

    componentDidMount() {
        this.write();
    }

    write(){
        const phrases = this.state.phrases;
        const pindex = this.state.phraseIndex;
        const phrase = phrases[pindex];

        if(!phrase){
            const psl = phrases.length;
            this.applyWrite(phrases[psl - 1].length, psl, 0);
            return;
        }

        const cindex = this.state.charIndex;

        if(cindex === phrase.length)
            this.applyWrite(pindex + 1, 0, phrases.length, 0);
        else this.applyWrite(pindex, cindex + 1, phrase[cindex] === " " ? 250 : 150);
    }

    applyWrite(phraseIndex, charIndex, wait){
        this.setState({
            phraseIndex,
            charIndex})

        if(wait > 0)
            setTimeout(() => this.write(), wait / this.state.speed)
    }

    textStyle(ncolor, bcolor){
        const style = { ...this.state.textStyle};
        style.color = ncolor;
        if(bcolor) style.backgroundColor = bcolor;
        return style;
    }

    renderCursor(phrase){
        const index = this.state.charIndex;
        const before = phrase.substr(0, index);
        const current = phrase[index];
        const after = phrase.substr(index + 1, phrase.length)
        return (<>
            <span style={this.state.textStyle}>
                    {before}
            </span>
            <span style={this.textStyle(
                Color(this.state.textStyle.color).negate(),
                this.state.textStyle.color)}>
                {current}
            </span>
            <span style={this.textStyle("transparent")}>
                {after}
            </span>
        </>)
    }

    renderPhrase(phrase, index){
        const currentIndex = this.state.phraseIndex
        return (<span key={index}>{
            (currentIndex === index ? 
            this.renderCursor(phrase) :
            <span style={currentIndex > index ? 
                this.state.textStyle : this.textStyle("transparent")}>
                {phrase}
            </span>)
            }<br/> 
        </span>)
    }

    render(){
        console.log("render")
        return (
        <div style={this.props.style}>  
            {this.state.phrases.map((p, i) => this.renderPhrase(p, i))}
        </div>
        )
    }
}