import Canvas from "./canvas"
import Color from "color"

const Math2 = require("../Other/math2");

const colors = [
    { main: "green", secondary : [ "blue", "orange", "red", "brown", "white" ] },
    { main: "blue", secondary : [ "green", "orange", "red", "brown", "white" ] },
    { main: "orange", secondary : [ "green", "blue", "red", "brown", "white" ] },
    { main: "brown", secondary : [ "green", "blue", "orange", "red", "white" ] },
    { main: "red", secondary : [ "green", "blue", "orange", "brown", "white" ] },
    { main: "white", secondary : [ "green", "blue", "orange", "red", "brown" ] },
]
    
const sizes ={
    min: 20,
    max: 100
}

const textures = [
    "craters", "lines"
]

export default class Planet extends Canvas {

    constructor(){
        const size = Math2.random(sizes.min, sizes.max);
        super(size * 4, size * 4);

        this.radius = size;

        this.position = { 
            x: size * 2,
            y: size * 2,
        }

        this.colors = Math2.randomElement(colors);

    }

    draw(){
        this.core()
        this.planetTexture()

        return {
            image: super.draw(),
            radius: this.radius,
        }
    }

    core(){

        let color = Color(this.colors.main);
        color = color.darken(Math2.random(0.2, 0.8))

        this.context.fillStyle = color.toString();
        this.context.strokeStyle = 'transparent';
        this.context.shadowBlur = 10;
        this.context.shadowColor = colors.toString();

        this.context.save();

        this.context.beginPath();
        this.context.arc(this.position.x, this.position.y, this.radius, 0, 2 * Math.PI);
        this.context.fill();
        this.context.stroke();
        this.context.closePath();

        this.context.restore();
    }

    planetTexture(){

        const start = { 
            x: this.position.x - this.radius,
            y: this.position.y - this.radius,
        }

        const end = {
            x: start.x + (this.radius * 2),
            y: start.y + (this.radius * 2),
        }

        this.context.save();
        this[`${Math2.randomElement(textures)}_Texture`](start, end);
        this.context.restore();
    }

    craters_Texture(start, end){
        const spacing = this.radius / 2;
        const pos = { x:start.x, y:start.y };

        const color2 = Color(Math2.randomElement(this.colors.secondary));
        const color = Color(this.colors.main).mix(color2, Math2.random(0.2, 0.5)).darken(0.6)

        this.context.globalCompositeOperation = "source-atop";

        while(pos.y < end.y){

            while(pos.x < end.x){   
                const size = Math2.random(this.radius / 8, this.radius / 2);
                this.context.fillStyle = color;
                this.context.shadowBlur = size / 4;
                this.context.shadowColor = color;
        
                this.context.beginPath();
                this.circle(pos.x, 
                    pos.y + Math2.random(-spacing, spacing), 
                    size);
                
                this.context.fill();

                pos.x += Math2.random(spacing, spacing * 2);
            }

            pos.y += Math2.random(spacing, spacing * 2);
            pos.x = start.x;
        }
    }

    lines_Texture(start, end){
        let pos = start.y;
        this.context.globalCompositeOperation = "source-atop";
        const rotation = Math2.random(-this.radius, this.radius);
        let thickness;
        while(pos < end.y){
 
            const color2 = Color(Math2.randomElement(this.colors.secondary));
            const color = Color(this.colors.main).mix(color2, Math2.random(0.2, 0.5));

            this.context.strokeStyle = color;

            thickness = Math2.random(1, 5);
            this.context.lineWidth = thickness

            this.context.beginPath();
            this.context.moveTo(start.x, pos + rotation)
            this.context.lineTo(end.x, pos - rotation);
            this.context.closePath();
            this.context.stroke();
            

            pos += thickness;
        }
    }
}