import React from "react";


export default class Image extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            x: this.props.x || 0,
            y: this.props.y || 0,
        }
    }

    render(){
        if(!this.props.image) return null;
        return ( <img 
            style={this.props.style}
            src={this.props.image} alt={this.props.title || "Unamed"} draggable={false}
            /> )
    }
}