import Canvas from "./canvas"
const Math2 = require("../Other/math2")

export default class Stars extends Canvas{

    starColors = [ "white", "blue", "red", "orange" ]

    constructor(width, height){
        super(width, height);
    }
    
    draw(){
        this.background();
        this.stars(100);

        return super.draw();
    }

    background(){
        this.context.fillStyle = 'black';
        this.context.fillRect(0, 0, this.canvas.width, this.canvas.height); 
    }

    stars(spacing){
        const position = { x:0, y:0 }
        const width = this.canvas.width;
        const height = this.canvas.height;
        const sp = spacing * 2;
        while(position.y < height){
            while(position.x < width){
                this.star(Math2.random(1, 5), 
                position.x + Math2.random(-spacing, spacing), 
                position.y + Math2.random(-spacing, spacing))
                
                position.x += Math2.random(spacing, sp)
            }
            position.y += Math2.random(spacing, sp)
            position.x = 0;
        }
    }

    star(radius, x, y){
        this.context.fillStyle = 'white';
        this.context.shadowBlur = radius + 5;
        this.context.shadowColor = Math2.randomElement(this.starColors);

        this.context.beginPath();
        this.context.arc(x, y, radius, 0, 2 * Math.PI);
        this.context.stroke(); 
        this.context.fill();
    }
}