import React from "react";
import Button from "../../Button";

export default class Tabs extends React.Component{


    tabs = [];

    constructor(props){
        super(props);

        this.state = {
            tabNames: this.props.tabs || [ "Tab 1" ]
        }
    }

    onTabPress(key, selected){
        if(this.props.onTabChange)
            this.props.onTabChange(key, selected);
        
        for(const key in this.tabs){
            this.tabs[key].setState({selected:false});
        }
    }

    render(){
        return (
        <div className="tabContainer">
            {this.state.tabNames.map((name, key) => 
                <Button ref={c => this.tabs[key] = c} key={key} title={name} onPress={(s) => this.onTabPress(key, s)} />    
            )}
        </div>
        )
    }
}