import React, { useState } from 'react'
import "./style.css"

export default class CardFlip extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            flips: 0,

            front: 0,
            back: this.nextIndex(0),
        }
    }

    nextIndex(current){
        return current + 1 < this.props.children.length ? 
            current + 1 : 0;
    }

    onClick(){
        const flips = this.state.flips;
        console.log(flips, flips % 2 != 0);
        const front = flips % 2 != 0 ? this.nextIndex(this.state.back) : this.state.front;
        const back = flips % 2 == 0 ? this.nextIndex(this.state.front) : this.state.back;
        this.setState({
            flips: this.nextIndex(flips),
            front, back
        });

        if(this.props.onClick)
            this.props.onClick(front, back);
    }

    transitionStyle(angle){
        return { 
            transition: 'all 500ms ease-in-out', 
            transform: `rotateY(${angle}deg)`
        }
    }

    render(){
        const children = this.props.children;
        if(children.length < 1) return null;

        const angle = this.state.flips * 180;

        return (
        <div className={this.props.className} style={this.props.style}
            onClick={() => this.onClick()}>
            <button className="flip-card-container">
                <div className="flip-card-flipper" >
                    <div className="flip-card-front" 
                    style={this.transitionStyle(angle)}>
                        {children[this.state.front]}
                    </div>
                    <div className="flip-card-back" 
                    style={this.transitionStyle(angle + 180)}>
                        {children[this.state.back]}
                    </div>
                </div>
            </button>
        </div>
        )
    }
}