import React from "react";
import "./style.css"

export default class Projects extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            info: this.verifyInfo(props.info),
            transform: props.transform,
        }
    }

    verifyInfo(info){
        info.title = info.title || "Unnamed";

        return info;
    }

    render(){
        const info = this.state.info;
        return (
        <div className="projectContainer">
            <span className="whiteText">{info.title}</span>
            <p className="whiteText">{info.description}</p>
            <image src={info.image} alt={info.title + "_Image"} />
            <div>
                <span className="whiteText">Using: </span>
                <span className="whiteText">
                    {info.tech.join(', ')}
                </span>    
            </div>
            <a className="whiteText" href={info.link.ref}>{info.link.name || "Link"}</a>
        </div>
        )
    }
}