import React from "react";
import "./style.css"

import Color from "color"
import TerminalBackground from "../../Drawing/terminalbk";

import Controls from "./Controls"
import HologramicBar from "./HologramicBar";

export default class Terminal extends React.Component{

    constructor(props){
        super(props);

        const percent = 0.99;
        const size = Math.min(window.innerWidth, window.innerHeight) * percent;
        this.state = {
            percent,
            size: {
                s: size,
                x: size * 1.1,
                y: size * 0.9
            },
            horizontal: window.innerWidth >= window.innerHeight,
        };
        this.state.terminalbk = this.getTerminalBk()
    }

    borderStyle(){
        const border = 10 * this.state.percent;
        const size = this.state.size.s;
        console.log(size);
        return {
            backgroundImage: this.grad(Color("#232925")),

            flexDirection: "column",

            width: this.state.size.x,
            height: this.state.size.y,
            borderWidth: border,
            borderRadius: size / 8,
            borderColor: Color("#484E4B").lighten(0.3),
            margin: 5,
        }
    }

    innerStyle(percent = 85, color = "blue", lighten = 0.3, borderWidth = 0, radius = 10){
        const cColor = Color(color);
        const style = {
            width: `${percent}%`,
            height: `${percent * 0.95}%`,
            backgroundImage: this.grad(cColor),
        }

        if(borderWidth >= 0){
            style.borderColor = cColor.lighten(lighten)
            //style.borderImage = this.grad(cColor.lighten(lighten));
            style.borderStyle = "solid";
            style.borderWidth = borderWidth;
            style.borderRadius = radius;
        }

        return style;
    }

    grad(color){
        return `linear-gradient(to top left, ${color.lighten(0.2)}, ${color.darken(0.6)})`
    }

    getTerminalBk(){
        try{
            const terminalbk = new TerminalBackground(this.state.size.x, this.state.size.y);
            const img = terminalbk.draw();
            return img;
        }catch(e){
            console.error(e);
        }
    }



    render(){
        //width:this.state.size.x, height:this.state.size.y
        return (
        <div className="main">
            <div className="container" style={{flexDirection: 
                (!this.state.horizontal ? "column" : "row") }}>
                {this.state.horizontal ? <Controls options={this.state} /> : null}
                <div className="center border" style={this.borderStyle()}>
                    <label className="name">Maxime Gautier</label>
                    <div  className="center" style={this.innerStyle(95, "#484E4B", 0.3, 8, this.state.size.s / 8)}>
                        <div  className="center" style={this.innerStyle(97, "green", -0.3, 1, this.state.size.s / 5)}>
                            <label style={{position:"fixed", zIndex:0, width:"90%"}}>
                            Banana, Orange, Cream, Sunset, Your mom, I love them all equally. Some text some text
                            some text some text some text some textsome textsome textsome text  some text some text 
                            some text some text some text some text some text some text some text some text some text 
                            some text some text some text some text some text some text some text some text some text
                            </label>

                            <img style={{zIndex:1}} src={this.state.terminalbk} />
                            <HologramicBar />
                            <label>Banana</label>
                        </div>
                    </div>
                </div>
                {!this.state.horizontal ? <Controls options={this.state}/> : null}
            </div>
        </div>
        )
    }
}