import React from "react";
import Tabs from './tabs'
import './style.css'

import TerminalBackground from "../../../Drawing/terminalbk";

function getTerminalBk(){
    try{
        const size = Math.max(window.innerHeight, window.innerWidth)
        const terminalbk = new TerminalBackground(size, size);
        const img = terminalbk.draw();
        return img;
    }catch(e){
        console.error(e);
    }
}

const terminalbk = getTerminalBk()

export default class TabHandler extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            tabs: this.props.tabs,
            activeTab: -1,
            nextTab: -1,
            event:-1,

            
        }
    }

    onChangeTab(tabKey){
        let event = this.state.event;
        if(tabKey < 0) //Closing
            this.setState({nextTab:tabKey, event:-1});
        else if(event === -1) //Opening
            this.setState({activeTab:tabKey, event:1});
        else //Switching
            this.setState({nextTab:tabKey, event: 0});
    }

    scaleStyle(){
        const close = this.state.event < 1;
        return {
            transform: `scale(${close ? 2 : 1}, ${close ? 0 : 1})`,
        }
    }

    onTransitionEnd(){
        const event = this.state.event;
        if(event === 0) this.setState({activeTab:this.state.nextTab, nextTab:-1, event:1});
    }

    render(){
        const tab = this.state.activeTab;
        const hasTab = this.props.children && tab > -1 && tab < this.props.children.length;
        return (
        <div className="tabHandler">
            <Tabs tabs={this.state.tabs} onTabChange={(key, selected) => this.onChangeTab(selected ? key : -1)} />
            <div className={`tabContentContainer${hasTab ? " blur" : ""}`}
                style={this.scaleStyle()}
                onTransitionEnd={() => this.onTransitionEnd()}
            >
                <div className="tabContentInner">
                    {hasTab ? this.props.children[tab] : null}
                </div>
                <div className="tabScreenContainer">
                    <img style={{pointerEvents:"none", flex:1}} src={terminalbk} />
                </div>
            </div>
        </div>
        )
    }
}