import React from "react";
import CardFlip from "../../../CardFlip";
import TypedText from "../../../TypedText"

export default class LanguageTools extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            title: props.title || "Untitled",
            tools: props.tools || [ "--" ]
        }
    }


    renderNorma(){
        return (
        <div className="tntLangContainer">
            <span className="tntTitle whiteText">{this.state.title}</span>
            <div className="tntToolList">
                {this.state.tools.map((item, index) => 
                    <span className="tntToolName whiteText" key={index}>- {item}</span>
                )}
            </div>
        </div>
        )
    }

    render(){
        return (
        <div className="tntLangContainer">
            <CardFlip style={{display:"flex", width:"100%", height:"100%",
                justifyContent:"center", alignItems:"center"
            }}>
            <div className="tntToolList" style={{height:"100%"}}>
                <TypedText 
                    text={this.state.title}
                    textStyle={{color:"#31CB00", textDecoration:"underline", fontWeight:"bold"}}
                    className="tntTitle whiteText"/>    
            </div>
            <div className="tntToolList">
                {this.state.tools.map((item, index) => 
                    <span className="tntToolName whiteText" key={index}>{item}</span>
                )}
            </div>
            </CardFlip>
        </div>
        )
    }
}