import React from "react";
import TypedText from "../../TypedText";
import "./style.css"

const data = {
    first: true,
    text:
    "Hello, I'm Maxime Gautier and welcome to my portfolio! \n" +
    "I'm a computer science student currently working as a freelance software developer."
}

export default class Intro extends React.Component{

    constructor(props){
        super(props);

        this.state = {

        }
    }

    render(){
        if(data.first){
            data.first = false;
            return (<div className="cText" style={{flex:2, padding:10}}>
                <TypedText 
                    speed={4}
                    textStyle={{color:"#31CB00"}}
                    text={data.text}
                />
            </div>)
        }

        return (
            <div className="cText" style={{color:"#31CB00",flex:2, padding:20}}>
                {data.text.split('\n').map(t => 
                    <>{t}<br/></>    
                )}
            </div>
        )
    }
}