import React from "react";
import Image from "../../../Image";
//import "./style.css"

export default class Planet extends React.Component{

    constructor(props){
        super(props);

        const radius = this.props.planet.radius;

        this.state = {
            image: this.props.planet.image,
            radius: radius,
            x: (this.props.x - radius) || 0,
            y: (this.props.y - radius) || 0,
        }
    }


    render(){
        return (
        <>
            <Image 
                style={{position:"absolute", left:this.state.x, top:this.state.y, zIndex: 1}}
                image={this.state.image} />
        </>
        )
    }
}