import React from "react";
import "./style.css"

import Color from "color"
import Button from "../../Button";

const buttons = [ "Test 1", "Test 2", "Test 3", "Test 4", "Tedt 5" ]

export default class Controls extends React.Component{

    constructor(props){
        super(props);

        this.state = this.props.options;
    }


    borderStyle(){
        const border = 10 * this.state.percent;
        const h = this.state.horizontal;
        return {
            backgroundColor: "#232925",
            flexDirection: !h ? "row" : "column",
            borderWidth: border,
            borderRadius: this.state.size.s / 30,
            borderColor: Color("#484E4B").lighten(0.3)
        }
    }

    render(){
        return (
        <div className="controls_border" style={this.borderStyle()}>
            <div style={{display:"flex", flexDirection: !this.state.horizontal ? "row" : "column", justifyContent:"space-evenly"}}>
                {buttons.map((name, i) => <Button key={i} horizontal={this.state.horizontal} title={name} /> )}
            </div>
            
        </div>
        )
    }
}