import React from "react";
import LanguageTools from "./languageTools";

const items = [ 
    {
        language: "Languages",
        tools: [ "C#", "JavaScript", "Java", "SQL", "TypeScript", "Shell/Bash", "VBA", "Python", "Lua" ]
    },
    {
        language: "Front End",
        tools:[ "HTML/CSS/SASS", "JavaFX", "React/React Native", "Bootstrap", "JQuery"  ]
    },
    {
        language: "Back End",
        tools:[ "Node.js", "MongoDB", 
            ".Net/Core", "ASP.Net", 
            "Flask", "SQL Alchemy" ]
    },
    {
        language: "Tools",
        tools:[ "Git/Github", "Unity", "Google Cloud Platform",  ]
    },
]

export default class TechNTools extends React.Component{

    constructor(props){
        super(props);

        this.state = {

        }
    }


    render(){
        return (
        <div className="tntContainer" style={{flex:1}}>
            <span className="whiteText">Languages, Tech and Tools</span>
            <div className="tntItemsContainer">
                {items.map((item, index) => 
                    <LanguageTools key={index} title={item.language} tools={item.tools} />
                )}
            </div>
        </div>
        
        )
    }
}